        updateBillingFrequencyOptions(planType = "2") {
            const vm = this;
            if (planType == "2") {
                vm.billingFrequencyOptions = vm.billingFrequencyMasterOptions.filter(
                    (item) => item.value !== "One Time"
                );
                vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", false);
                 //this.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", false);
            } else {
                vm.billingFrequencyOptions = [...vm.billingFrequencyMasterOptions];
                vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", true);
                 //vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", true);
                //  if (vm.refCode != 'Clone') {
                //      vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", true);
                //  }
            }
            if(vm.billingTypeOptions[0].FEATURE_CODE == "SUBSCRIPTION_BILLING")
            {
                vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", false);
            }
            
           const billingFrequency = vm.$refs.validatePlan.getPropertyValue("BillingFrequency", "value");
            if(billingFrequency === 'One Time'){
                 this.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", false);
            }
            vm.$refs.validatePlan.updateProperty("BillingFrequency", "config.options", vm.billingFrequencyOptions);
            vm.$refs.validatePlan.UpdateKeyValue();
        },
        onChangePlanType: function (x, y) {
            this.updateBillingFrequencyOptions(y.value);
        },
        Close: function () {
            var vueObj = this;  
            if (vueObj.refCode == 'Update' || vueObj.refCode == 'Clone') {
                vueObj.$parent.isCheckEditPlan = false;
                vueObj.$parent.isEditPlan = false;
                vueObj.$parent.refCode == '';
            }
            else {
                vueObj.$parent.isEditPlan = false;
            }
        },
        CloseTemplate: function () {
            var vueObj = this;
            vueObj.isShowReport = false;
        },
        BindEditDetail: function (editDetail) {
            debugger;
            var vm = this;
            if (editDetail[0].PLAN_IMAGE_URL) {
                let url = new URL(editDetail[0].PLAN_IMAGE_URL);
                let filePath = url.pathname.startsWith('/') ? url.pathname : '/' + url.pathname;
                let attachmentPath = filePath.replace(/^\/+/, '');
                let attachmentName = attachmentPath.split('/').pop();
                var attachmentData = { attachmentName: attachmentName, attachmentPath: attachmentPath };
                if (attachmentPath != null && attachmentName != null) {
                    vm.FormSchema[3].Data[0].config.showSelectedFiles.push(attachmentData);
                }
            }
            if (editDetail[0].PLAN_CODE == "UTILITY_BILLING") {
                vm.FormSchema[0].Data[1].visibility = true;
                vm.FormSchema[0].Data[2].visibility = true;
            }
            else {
                vm.FormSchema[0].Data[1].visibility = false;
                vm.FormSchema[0].Data[2].visibility = false;
            }
            vm.$refs.validatePlan.updateProperty("BillingTypeOptions", "value", editDetail[0].PLAN_CODE);
            vm.$refs.validatePlan.updateProperty("meterTypeId", "value", editDetail[0].METER_TYPE_ID,);
            vm.$refs.validatePlan.updateProperty("IsForPlan", "value", editDetail[0].IS_FOR_PLAN);
            editDetail[0].IS_FOR_PLAN == 1 ? vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", false) : vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", true);
            // if (editDetail[0].IS_FOR_PLAN == 1 && vm.refCode != 'Clone') {
            //     vm.$refs.validatePlan.updateProperty("proratedinvoice", "visibility", true);
            //     vm.$refs.validatePlan.updateProperty("proratedinvoice", "value", editDetail[0].IS_PRORATED_INVOICE);
            // }
            vm.$refs.validatePlan.updateProperty("PlanName", "value", editDetail[0].PLAN_NAME);
            vm.$refs.validatePlan.updateProperty("Status", "value", editDetail[0].STATUS_ID);
            vm.$refs.validatePlan.updateProperty("InvoiceTemplate", "value", editDetail[0].INVOICE_TEMPLATE_ID);
            vm.$refs.validatePlan.updateProperty("EffectiveDate", "value", editDetail[0].EFFECTIVE_DATE);
            vm.$refs.validatePlan.updateProperty("ExpirationDate", "value", editDetail[0].EXPIRATION_DATE);
            vm.$refs.validatePlan.updateProperty("CustomerType", "value", editDetail[0].CUSTOMER_SEGMENT_ID);
            vm.$refs.validatePlan.updateProperty("Description", "value", editDetail[0].PLAN_DESCRIPTION);
            vm.$refs.validatePlan.updateProperty("BillingFrequency", "value", editDetail[0].PAYMENT_TYPE);
            if (editDetail[0].PAYMENT_TYPE === "One Time") {
                if(editDetail[0].PLAN_CODE == "SUBSCRIPTION_BILLING")
                {
                    vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", false);
                    vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", false);
                }
                else
                {
                vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", false);
                vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", false);
                }

            } else {
                if(editDetail[0].PLAN_CODE == "SUBSCRIPTION_BILLING")
                {
                vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", false);
                
                vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", true);
                
                }
                else if(editDetail[0].PLAN_CODE == "UTILITY_BILLING") {
                    vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", false);
                    vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", false); 
                }
                else
                {
                    vm.$refs.validatePlan.updateProperty("NoOfDays", "visibility", true);
                    vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "visibility", true); 
                
                }

            }
            vm.$refs.validatePlan.updateProperty("no_of_plan", "value", editDetail[0].no_of_plan);
            vm.$refs.validatePlan.updateProperty("NoOfDays", "value", editDetail[0].NoOfDays);
            this.onNoOfDaysChange(editDetail[0].NoOfDays);
            if (editDetail[0].NoOfDays == 1) {
                vm.$refs.validatePlan.updateProperty("CustomDaysField", "visibility", true);
                vm.$refs.validatePlan.updateProperty("CustomDaysField", "value", editDetail[0].no_of_plan); 

            } else {
                vm.$refs.validatePlan.updateProperty("CustomDaysField", "visibility", false);
            }
            
            
            vm.$refs.validatePlan.updateProperty("isShowWebsite", "value", editDetail[0].IS_SHOW_ON_WEBSITE == true ? "1" : "0");
            vm.$refs.validatePlan.updateProperty("isShowPricing", "value", editDetail[0].IS_SHOW_ON_PRICING == true ? "1" : "0");
            vm.$refs.validatePlan.updateProperty("proratedinvoice", "value", editDetail[0].IS_PRORATED_INVOICE == true ? "1" : "0");
            //debugger
            // vm.$refs.validatePlan.updateProperty("AdditionalFrequency", "value",Jso editDetail[0].PLAN_ADDITIONAL_FREQUENCIES);
            // if (editDetail[0].planAdditionalFrequencies && Array.isArray(editDetail[0].planAdditionalFrequencies)) {
            //     this.frequencyOptions.forEach(f => {
            //         f.checked = false;
            //         f.discount = '';
            //     });

            //     editDetail[0].planAdditionalFrequencies.forEach(selectedFreq => {
            //         const match = this.frequencyOptions.find(f => f.frequency === selectedFreq.frequency);
            //         if (match) {
            //             match.checked = true;
            //             match.discount = selectedFreq.discount;
            //         }
            //     });
            // }

               if (editDetail[0].ADDITIONAL_FREQUENCY) {
                    // const frequencies = JSON.parse(`[${editDetail[0].ADDITIONAL_FREQUENCY}]`);
                    const frequencies =  JSON.parse(`[${editDetail[0].ADDITIONAL_FREQUENCY}]`)
                    //debugger
                    vm.frequencyOptions = vm.frequencyOptions.map(option => {
                        // find if backend has this frequency
                        const backendFreq = frequencies.find(f => f.frequency === option.frequency);

                        if (backendFreq) {
                            return {
                            ...option,
                            discount: backendFreq.discount || "",
                            checked: backendFreq.frequency ? true : false,       // selected if discount is present
                            disabled: !backendFreq.frequency         // disable if no discount
                            };
                        } else {
                            return {
                            ...option,
                            discount: "",
                            selected: false,
                            disabled: true
                            };
                        }
                        });
                    // vm.frequencyOptions = frequencies.map(f => ({
                    //     frequency: f.frequency,
                    //     discount: f.discount,
                    //     checked: f.discount ? true : false, 
                        
                        
                    // }));
                }

                

    vm.$refs.validatePlan.UpdateKeyValue();


        },
        GetDDLCustomerTypeData: function () {
            var vm = this;
            var params = `id=&moduleName=&type=CUSTOMER_TYPE&search=`
            DataService.GetDDLData(params).then((response) => {
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validatePlan.updateProperty("CustomerType", "config.options", response.data.DATA);
                        vm.$refs.validatePlan.UpdateKeyValue();
                    }
                }
            })
        },
        GetDDLInvoiceTemplateData: function () {
            var vm = this;
            // changes done by Avtar(07-jan-2025): Type changes  BIL_TEMPLATE -> PLAN_BILL_TEMPLATE
            var params = `id=&moduleName=&type=PLAN_BILL_TEMPLATE&search=`
            DataService.GetDDLData(params).then((response) => {
                
                if (response.data) {
                    var json = response.data;
                    if (json != null) {
                        vm.$refs.validatePlan.updateProperty("InvoiceTemplate", "config.options", response.data.DATA);
                        vm.$refs.validatePlan.UpdateKeyValue();
                    }
                }
            })
        },
   async SaveData() {
    debugger;
    const vm = this;
    const result = await vm.$refs.validatePlan.validateForm.validate();
    if (!result) return;

    let meterTypeId = vm.$refs.validatePlan.getPropertyValue("meterTypeId", "value");
    let isShowWebsite = vm.$refs.validatePlan.getPropertyValue("isShowWebsite", "value") == "1";
    let isShowPricing = vm.$refs.validatePlan.getPropertyValue("isShowPricing", "value") == "1";
    let isForPlan = vm.$refs.validatePlan.getPropertyValue("IsForPlan", "value");
    let planName = vm.$refs.validatePlan.getPropertyValue("PlanName", "value");
    let status = vm.$refs.validatePlan.getPropertyValue("Status", "value");
    let invoiceTemplate = vm.$refs.validatePlan.getPropertyValue("InvoiceTemplate", "value");
    let customerType = vm.$refs.validatePlan.getPropertyValue("CustomerType", "value");
    let effectiveDate = vm.$refs.validatePlan.getPropertyValue("EffectiveDate", "value");
    let expirationDate = vm.$refs.validatePlan.getPropertyValue("ExpirationDate", "value");
    let description = vm.$refs.validatePlan.getPropertyValue("Description", "value");
    let billingType = vm.$refs.validatePlan.getPropertyValue("BillingTypeOptions", "value");
    let billingFrequency = vm.$refs.validatePlan.getPropertyValue("BillingFrequency", "value");
    let proratedinvoice = vm.$refs.validatePlan.getPropertyValue("proratedinvoice", "value") == "1";
    let noOfDays = Number(vm.$refs.validatePlan.getPropertyValue("NoOfDays", "value"));
    let noOfPlan = null;

    if (noOfDays === 1) {
        noOfPlan = vm.$refs.validatePlan.getPropertyValue("CustomDaysField", "value");
    }

   let selectedFrequencies = [];

    if ((billingFrequency || "").toLowerCase() !== "one time") {
        selectedFrequencies = vm.frequencyOptions
            .filter(f => f.checked)
            .map(f => ({
                plan_additional_frequency_id: f.id || 0,
                plan_id: vm.plan_Id || 0,
                frequency: f.frequency,
                discount: f.discount||0,
                status_id: 1
            }));
    }

    const isInsert = vm.plan_Id === 0 || vm.plan_Id === null || vm.plan_Id === undefined;

    const objData = {
        id: isInsert ? 0 : vm.plan_Id,
        meterTypeId: meterTypeId,
        isShowWebsite: isShowWebsite,
        isShowPricing: isShowPricing,
        isForPlan: isForPlan,
        planName: planName,
        planCode: billingType,
        status: status,
        invoiceTemplate: invoiceTemplate,
        effectiveDate: effectiveDate,
        expirationDate: expirationDate,
        customerType: customerType || "0",
        description: description,
        billingFrequency: billingFrequency,
        isproratedinvoice: proratedinvoice,
        attachment: JSON.stringify(vm.fileData),
        NoOfPlan: noOfPlan,
        NoOfDays: noOfDays,
        planAdditionalFrequencies: selectedFrequencies
    };

    let serviceCall = vm.refCode === 'Clone'
        ? DataService.ClonePlan(objData)
        : DataService.AddUpdatePlan(objData);

    await serviceCall.then(response => {
        if (response.data && response.data.Status === "Success") {
            const plan_Id = response.data.Result;
            const routeName = vm.billingTypeOptions[0].FEATURE_CODE === "SUBSCRIPTION_BILLING"
                ? 'ManageSubscriptionPlanDetail'
                : 'ManagePlan';

            if (response.data.Code === "1") {
                vm.$router.push({ name: routeName, params: { id: plan_Id } });
                vm.ShowAlert(vm.$t('AddedPlan'), "success");
            } else if (response.data.Code === "2") {
                vm.ShowAlert(vm.$t('UpdatedPlan'), "success");
            } else if (response.data.Code === "4") {
                vm.ShowAlert(vm.$t('AlreadyExists', [vm.$t('Plan')]), "failure", true, vm.$t('Alert'));
                return;
            }

            vm.Close();
            if(vm.IsForDetail=='fromDetail'){
                 vm.$parent.refreshComponent++;
            }else{
                vm.$parent.FetchData();
            }
        } else {
            vm.ShowAlert(vm.$t("UnknownError"), "failure", vm.$t('Alert'));
        }

        vm.isLoading = false;
    });
}

    }
}
